<template>
  <div id="app" class="app">
    <div class="app__content">
      <vue-book-effects
        class="book-effects"
        ref="bookEffects"
        :flip-duration="1000"
        :pages="pages"
        :startPage="currentBookOptions.page"
        @flip-right-end="onFlipRightEnd"
      />
    </div>
    <FearFooter :current-book-options="currentBookOptions" @modal="visibleModal" />
    <FearModal
        v-show="isModalVisible"
        :chapters="chapters"
        @to="handleClickPushToPage"
        @close="visibleModal"
    />
    <FearAboutMe />
  </div>
</template>

<script>
import VueBookEffects from "vue-book-effects";
import FearModal from "@/components/FearModal.vue";
import FearAboutMe from "@/components/FearAboutMe.vue";
import FearFooter from "@/components/FearFooter.vue";

export default {
  name: "App",
  components: {FearFooter, FearAboutMe, FearModal, VueBookEffects },
  data() {
    return {
      currentBookOptions: {
        page: 1,
        label: 'арты',
      },
      isModalVisible: false,
      pages: [],
      isNotebookView: true,
      chapters: [
        { label: 'арты', page: 1 },
        { label: 'графити', page: 2 },
        { label: 'стикеры', page: 3 },
        { label: 'наброски', page: 4 },
        { label: 'живопись', page: 5 },
        { label: 'айдентика', page: 6 },
        { label: 'полиграфия', page: 7 },
        { label: 'инфографика', page: 8 },
        { label: 'коммерческие проекты', page: 9 },
      ]
    };
  },
  mounted() {
    setTimeout(() => {
      this.pages = [
        null,
        require("@/assets/обложка.jpg"),
        require("@/assets/1.jpg"),
        require("@/assets/2.jpg"),
        require("@/assets/3.jpg"),
        require("@/assets/4.jpg"),
        require("@/assets/7.jpg"),
        require("@/assets/8.jpg"),
        require("@/assets/9.jpg"),
        require("@/assets/11.jpg"),
        require("@/assets/12.jpg"),
      ]
    }, 1)
  },
  methods: {
    // Метод вызова переключения страниц к ХХ разделам портфолио
    handleClickPushToPage({ page, label }) {
      this.currentBookOptions.page = page;
      this.currentBookOptions.label = label;

      this.$nextTick(() => {
        this.visibleModal();
      })
    },
    onFlipRightEnd(lastPage) {
      this.currentBookOptions.label = this.chapters.find(page => page.page === lastPage).label;
    },
    visibleModal() {
      this.isModalVisible = !this.isModalVisible;
    },
  }
};
</script>

